import { NgModule } from '@angular/core';
import { SharedModule } from '../shared';

import { FastComponent } from './fast.component';

import { FastRoutingModule } from './fast-routing.module';
import { GamePlayerComponent } from './game-player/game-player.component';

@NgModule({
  declarations: [
    FastComponent,
    GamePlayerComponent
  ],
  imports: [
    SharedModule,
    FastRoutingModule
  ],
  providers: [],
  entryComponents: [FastComponent]
})
export class FastModule { }


